
body { 
  background-image: url('fundo.png');
  background-size: cover;
  background-repeat: no-repeat;
  font-family: 'Arial', sans-serif;
  background-color: #0a0a0a;
  color: #ddd;
  margin: 0;
  padding: 0;
}

.container {
  padding: 20px;
}

header h1 {
  color: #1aa7b9;
  text-align: center;
  margin-bottom: 20px;
}

.quick-links {
  display: flex;
  justify-content: flex-start;  
  gap: 20px;
  margin-top: 20px;
}

.quick-links .btn {
  background-color: #131212; 
  color: white;
  padding: 10px 20px;
  text-decoration: none;
  border-radius: 5px;
  display: inline-flex;
  align-items: center;
  font-size: 16px;
  transition: background-color 0.3s ease;
}

.quick-links .btn:hover {
  background-color: #f80a0a; 
}

.quick-links .btn i {
  margin-right: 8px;
}

.quick-links .btn i.fa {
  font-size: 20px;
}

footer {
  text-align: center;
  position: fixed;
  bottom: 0;
  width: 100%;
  padding: 20px;
  font-size: 14px;
  color: #fffcfc;
}

footer p {
  margin: 0;
}


form input, form select, form textarea {
  width: 70%;
  padding: 15px;
  margin: 10px 0;
  border-radius: 10px;
  border: 1px solid #ccc;
}

/* Estilo do botão no formulário */
form button {
  width: 30%;
  background-color: #15c7df;
  color: white;
  padding: 10px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

form button:hover {
  background-color: #e71616;
}

/* Tabela */
table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
}

table th, table td {
  padding: 10px;
  border: 1px solid #ddd;
  text-align: left;
}

table th {
  background-color: #f8f9fa;
}

/* Botões de editar e deletar */
button.edit {
  background-color: #07ffff;
}

button.delete {
  background-color: #091dd4;
  color: white;
}
